.wrap {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  background-color: #f4f4f4;
  cursor: none;
}

/* header */
/* --- img part --- */
.header {
  position: relative;
  width: 100%;
  overflow: hidden;
  background-color: #2f61d2;
}
.bg {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 0;
  padding-bottom: calc(82% / 16 * 9);
  overflow: hidden;
}
.bg_img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  position: absolute;
  top: 0;
  left: 0;
  max-height: 90vh;
}
/* --- text part --- */

.project_info_wrap {
  margin: 0 auto;
  width: 90%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.project_info_div {
  display: flex;
  flex: 1 1 70%;
  width: 100%;
}
.project_info {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-right: 60px;
  width: max-content;
}
.project_info:last-child {
  margin-right: 30px;
}
.project_title {
  font-family: "Noto-Sans-KR";
  font-weight: 400;
  margin: 0 10px 0 0;
  color: #dfdfdf;
}
.project_desc {
  font-family: "Noto-Sans-KR";
  font-weight: 400;
  color: #fff;
}
.project_desc_m {
  line-height: 1.5;
  font-family: "Noto-Serif-KR";
  font-weight: 400;
  color: #fff;
}
.view_live_wrap {
  display: flex;
  align-items: center;
  flex: 1 1 30%;
  justify-content: flex-end;
}
.view_live_nav {
  flex: 1 1;
  text-decoration: none;
  text-align: end;
}
.view_btn {
  height: 40px;
  border: 1px solid #fff;
  border-radius: 20px;
  color: #fff;
  background: unset;
  outline: none;
  font-family: "Noto-Sans-KR";
  font-weight: 400;
  padding: 0 20%;
  font-size: 1rem;
  cursor: pointer;
  transition: all 0.6s ease-in-out;
}
.view_btn:hover {
  color: #2f61d2;
  background-color: #fff;
}
.git {
  margin-left: 30px;
}

/* project Review   */
.project_dec {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  width: 100%;
  padding: 0 0 6% 0;
  background-color: #2f61d2;
}

.project_dec_text_wrap {
  display: flex;
  flex: 1 1 70%;
  width: 90%;
  margin: 60px auto;
  line-height: 1.5;
  color: #fff;
}
.project_dec_title {
  width: 30%;
  font-size: 3.438rem;
  margin: 0 66px 0 0;
  color: #f4f4f4;
  line-height: 1;
  font-family: "Noto-Sans-KR";
  font-weight: 400;
}
.project_dec_text {
  font-family: "Noto-Serif-KR";
  font-weight: 400;
  font-size: 1.625rem;
  line-height: 1.7;
  color: #fff;
  margin: 0;
}
.view_live_wrap_m {
  display: none;
}

/* project detail */

/* info */
.project_detail {
  width: 100%;
}
.project_detail_desktop_wrap {
  width: 90%;
  margin: 100px auto;
  padding: 8% 0;
  background-color: #424242;
}
.project_detail_wrap {
  width: 100%;
  margin: 0 auto;
}

/* detail_preview_desktop */
.project_detail_desktop_browser_wrap {
  width: 75%;
  margin: 0 auto;
  box-shadow: 0 32px 64px 0 rgba(0, 0, 0, 0.4);
  background-color: #424242;
}
.project_img {
  width: 100%;
}
.project_img_ongoing {
  width: 100%;
}
.project_img_m {
  width: 100%;
  display: none;
}

.browser_content {
  position: relative;
  border-top: 1px solid #e1e1e1;
  overflow: hidden;
}

/* detail_preview_mobile */

.project_detail_desktop_dec,
.project_detail_mobile_dec {
  width: 50%;
  margin: 80px auto 100px;
}
.project_detail_mobile_wrap {
  width: 90%;
  margin: 0 auto;
  display: flex;
}
.project_detail_mobile,
.project_detail_tablet {
  width: 64.67066%;
  background-color: #424242;
}

.project_detail_mobile_des {
  width: 29.34132%;
  margin: 0 5.98802% 5.98802% 0;
}

.project_detail_mobile_text_wrap {
  width: 100%;
}

.project_detail_tablet_des {
  width: 29.34132%;
  margin: 0 0 5.98802% 5.98802%;
}

.project_detail_mobile_mv {
  width: 30%;
  margin: 5% auto;
  position: relative;
  box-shadow: 0 32px 64px 0 rgb(0 0 0 / 40%);
}
.project_detail_tablet_mv {
  width: 60%;
  margin: 5% auto;
  position: relative;
  box-shadow: 0 32px 64px 0 rgb(0 0 0 / 40%);
}

/* footer */
.footer {
  width: 100%;
  height: 300px;
  margin-top: 60px;
  background-color: #e6e6e4;
  position: relative;
  font-family: "Noto-Sans-KR";
  font-weight: 400;
}
.footer_img {
  width: 100%;
  height: 100%;
  position: absolute;
  overflow: hidden;
  top: 0;
  z-index: 1;
  background-color: #fff;
  display: flex;
  justify-content: center;
  align-items: center;
}
.footer_next_img {
  width: 300px;
  height: auto;
}

.next {
  z-index: 2;
  position: absolute;
  width: 100%;
}
.grad_next {
  background-color: rgb(0 0 0 / 12%);
  width: 100%;
  height: 100%;
  position: absolute;
  left: 0;
  top: 0;
}

.footer_btn {
  width: 60%;
  margin: 0 auto;
  height: 300px;
  display: flex;
  color: #424242;
  font-size: 1.125rem;
  flex-direction: column;
  justify-content: center;
}
.footer_p {
  display: flex;
  align-items: center;
  font-size: 1.125rem;
}
.dash {
  width: 26px;
  height: 1px;
  background: #424242;
  display: block;
  margin-right: 10px;
}
.footer_next {
  margin-top: 10px;
  font-size: 3.438rem;
  font-family: "Noto-Sans-KR";
  font-weight: 400;
}
.black {
  color: #424242;
}
.blue {
  color: #2f61d2;
}

@media screen and (max-width: 1440px) {
  .view_btn {
    font-size: 0.813rem;
  }
  .project_dec_title {
    font-size: 2.25rem;
  }
  .project_dec_text {
    font-size: 1.125rem;
  }
}

@media screen and (max-width: 1024px) {
  .view_live_wrap {
    display: none;
  }
  .view_live_wrap_m {
    display: flex;
    width: 100%;
    justify-content: center;
  }
  .git {
    margin: 0;
  }
  .view_live_nav {
    text-align: center;
  }
  .project_title,
  .project_desc {
    font-size: 0.813rem;
  }
  .project_img {
    display: none;
  }
  .project_img_m {
    display: block;
  }
}
@media screen and (max-width: 768px) {
  .project_info {
    margin-right: 30px;
  }
  .project_info:last-child {
    margin-right: 0px;
  }
  .project_dec_text_wrap {
    flex-direction: column;
  }
  .project_dec_title {
    margin-bottom: 15px;
  }
  .project_dec_text {
    font-size: 1rem;
  }
  .project_detail_desktop_wrap {
    margin: 50px auto;
  }
  .project_detail_mobile_wrap {
    flex-direction: column;
  }
  .project_detail_mobile_des,
  .project_detail_mobile_text,
  .project_detail_mobile,
  .project_detail_tablet_des,
  .project_detail_tablet {
    width: 100%;
  }
  .project_detail_tablet_des {
    margin: 50px auto 0;
  }
}

@media screen and (max-width: 767px) {
  .view_live_wrap {
    min-height: 100px;
  }
}
@media screen and (max-width: 600px) {
  .project_info_div {
    flex-wrap: wrap;
  }
  .project_title,
  .project_desc {
    margin: 0;
  }
  .view_btn {
    padding: 0 6%;
  }
  .footer_next {
    font-size: 2.375rem;
  }
}
@media screen and (max-width: 553px) {
  .footer {
    height: 200px;
  }
  .footer_btn {
    height: 200px;
  }
  .footer_next {
    margin-bottom: 15px;
  }
}
@media screen and (max-width: 480px) {
  .project_info_wrap {
    margin-top: 10px;
  }
}
@media screen and (max-width: 466px) {
  .project_info:last-child {
    margin-top: 5px;
  }
}

@media screen and (max-width: 384px) {
  .project_info:first-child {
    margin-right: 10px;
  }
  .project_info:nth-child(2) {
    margin: 0;
  }
  .project_info:last-child {
    margin-top: 5px;
  }
  .project_dec_title {
    font-size: 1.625rem;
  }
  .project_dec {
    margin-top: -20px;
  }
  .footer_next {
    font-size: 1.75rem;
  }
}
